<template>
  <div class="partner-company">
    <el-container>
      <el-header class="header" id="header">
        <!-- 搜索表单 start -->
        <el-form
          :inline="true"
          :model="searchValidateForm"
          ref="searchValidateForm"
          label-width="100px"
          class="search-validate-form"
          :rules="searchValidateFormRules"
        >
          <!-- 输入设备编号 start -->
          <el-form-item label="" prop="robotCode">
            <el-input
              v-model="searchValidateForm.robotCode"
              placeholder="请输入设备编号"
            ></el-input>
          </el-form-item>
          <!-- 输入设备编号 end -->

          <!-- 请选择日期统计方式 start -->
          <el-form-item label="" prop="">
            <el-select
              v-model="selectDateState"
              filterable
              placeholder="请选择日期统计方式"
              @change="onDateChange"
            >
              <el-option
                v-for="(item, index) in dateStateOptions"
                :key="index"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 请选择日期统计方式 end -->

          <!-- 输入起止时间 start -->
          <el-form-item label="" prop="startEndDate">
            <el-date-picker
              v-model="searchValidateForm.startEndDate"
              unlink-panels
              :type="dateType"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
            >
            </el-date-picker>
          </el-form-item>
          <!-- 输入起止时间 end -->

          <!-- 请选择检验类型 start -->
          <el-form-item label="" prop="type">
            <el-select
              v-model="searchValidateForm.type"
              filterable
              placeholder="请选择检验类型"
              clearable
            >
              <el-option
                v-for="(item, index) in typeOptions"
                :key="index"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 请选择检验类型 end -->

          <!-- 请选择检验结果 start -->
          <el-form-item label="" prop="result">
            <el-select
              v-model="searchValidateForm.result"
              filterable
              placeholder="请选择检验结果"
              clearable
            >
              <el-option
                v-for="(item, index) in resultOptions"
                :key="index"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 请选择检验结果 end -->
        </el-form>
        <!-- 搜索表单 end -->

        <!-- 搜索 start -->
        <el-button
          type="primary"
          icon="el-icon-search"
          @click="onSearch('searchValidateForm')"
          >搜索</el-button
        >
        <!-- 搜索 end -->

        <!-- 重置 start -->
        <el-button type="primary" icon="el-icon-refresh-right" @click="onReset"
          >重置</el-button
        >
        <!-- 重置 end -->

        <!-- 导出 Excel start -->
        <el-button
          type="primary"
          :loading="downloadLoading"
          icon="el-icon-top-right"
          @click="exportExcel"
          >导出检验单</el-button
        >

        <!-- 导出 Excel end -->
      </el-header>
      <el-main>
        <!-- 表格 start -->
        <el-table
          class="table"
          :data="tableData"
          style="width: 100%"
          border
          v-loading="tableLoading"
          @selection-change="handleSelectionChange"
          :header-cell-style="{
            background: '#f2f2f2',
            color: '#606266',
            height: '38px',
            padding: '0px'
          }"
          :cell-style="{ height: '38px', padding: '0' }"
        >
          <!-- 表格多选列 start -->
          <el-table-column type="selection" width="55" fixed="left">
          </el-table-column>
          <!-- 表格多选列 end -->
          <el-table-column
            v-for="(value, key) in tableFields"
            :key="key"
            :prop="value"
            :label="key"
            min-width="130"
          >
            <template slot-scope="scope">
              <div>
                {{ tableContentValue(scope.row, value) }}
              </div>
            </template>
          </el-table-column>
          <el-table-column label="操作" min-width="130" fixed="right">
            <template slot-scope="scope">
              <el-button
                @click.native.prevent="detailRow(scope.$index, scope.row)"
                type="text"
                size="small"
              >
                详情
              </el-button>
              <el-button
                @click.native.prevent="checklist(scope.$index, scope.row)"
                type="text"
                size="small"
              >
                检验单
              </el-button>
            </template>
          </el-table-column>
        </el-table>
        <!-- 表格 end -->

        <!-- 导出的表格 start -->
        <!-- 此表格始终保持隐藏 -->
        <el-table
          id="outTable"
          class="table"
          :data="exportTableData"
          style="width: 100%; display: none"
          border
        >
          <el-table-column
            v-for="(value, key) in tableFields"
            :key="key"
            :prop="value"
            :label="key"
            min-width="130"
            align="center"
          >
          </el-table-column>
        </el-table>
        <!-- 导出的表格 end -->
      </el-main>
      <el-footer>
        <!-- 分页 start -->
        <div class="block">
          <el-pagination
            background
            layout="prev, pager, next, total, sizes, jumper"
            :total="total"
            :current-page="currentPage"
            :page-sizes="[10, 20, 30, 40, 50, 60, 70, 80, 90]"
            :page-size="pageSize"
            @current-change="onCurrentChange"
            @size-change="onSizeChange"
          >
          </el-pagination>
        </div>
        <!-- 分页 end -->
      </el-footer>

      <!-- 检验单dialog start -->
      <el-dialog
        :title="checklistTitle"
        :visible.sync="dialogVisible"
        :before-close="handleClose"
        center
      >
        <div ref="checklistTable" v-loading="checklistLoading">
          <el-table
            :data="checklistTableData"
            border
            style="width: 100%"
            :show-header="false"
          >
            <el-table-column
              v-for="(value, key) in checklistTableFields"
              :key="key"
              :prop="value"
              :label="key"
            >
            </el-table-column>
          </el-table>

          <el-table
            :data="checklistTableData2"
            border
            style="width: 100%; margin-top: 50px"
          >
            <el-table-column label="序号" type="index" width="50">
            </el-table-column>
            <el-table-column
              v-for="(value, key) in checklistTableFields2"
              :key="key"
              :prop="value"
              :label="key"
            >
              <template slot-scope="scope">
                <div>
                  {{ checklistTableData2Scope(scope.row, value) }}
                </div>
              </template>
            </el-table-column>
          </el-table>
        </div>

        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button
            type="primary"
            icon="el-icon-download"
            @click="onDownloadPDF"
            :loading="downloadReportLoading"
            >下 载</el-button
          >
          <a :href="pdfUrl" :download="pdfName" id="a_pdf"></a>
        </span>
      </el-dialog>
      <!-- 检验单dialog end -->

      <!-- 详情 dialog start -->
      <el-dialog
        class="dialog"
        title=""
        :visible.sync="detailDialogVisible"
        :before-close="detailHandleClose"
        :close-on-click-modal="false"
      >
        <el-descriptions title="机器人检验记录详情信息" border>
          <el-descriptions-item
            v-for="(item, index) in detailTableList"
            :key="index"
            :label="index"
            >{{ item }}</el-descriptions-item
          >
        </el-descriptions>
      </el-dialog>
      <!-- 详情 dialog end -->
    </el-container>
  </div>
</template>

<script>
// import JsonExcel from 'vue-json-excel'
import FileSaver from 'file-saver'
import XLSX from 'xlsx'

import { formatDateTime } from '../utils/utils.js'
export default {
  name: 'RobotCheckRecord',
  data() {
    return {
      tableFields: {
        //导出Excel表格的表头设置
        检验单编号: 'id',
        检验类型: 'type',
        检验标准: 'inspectionName',
        检验结果: 'result',
        生产批次: 'productionInfo',
        设备编号: 'robotCode',
        机器人型号: 'robotModel',
        检验员: 'inspectorName',
        角色: 'inspectorRole',
        检验时间: 'inspectonTime',
        备注: 'notes'
      },
      tableData: [], // 表格数据

      checklistTableFields: {
        '1': '1',
        '2': '2',
        '3': '3',
        '4': '4'
      }, // 检验单表头
      checklistTableData: [
        {
          1: '公司名称',
          2: '北京九星智元科技有限公司',
          3: '设备编号',
          4: '2000001'
        },
        {
          1: '设备型号',
          2: '3.3',
          3: '生产批次',
          4: '202108-JH-001'
        },
        {
          1: '质检标准',
          2: '质检标准-2021002',
          3: '质检结果',
          4: '合格'
        },
        {
          1: '质检员',
          2: '张三',
          3: '质检日期',
          4: '2021-08-10'
        }
      ], // 检验单表格数据

      checklistTableFields2: {
        检验项: 'project',
        技术参数: 'parameter',
        检验结果: 'qualified'
      }, // 检验单表头2
      checklistTableData2: [
        {
          project: '刹车',
          parameter: '1.2m',
          qualified: '合格'
        },
        {
          project: '主刹',
          parameter: '1.2m',
          qualified: '合格'
        }
      ], // 检验单表格数据2

      dateStateOptions: [
        {
          value: '1',
          label: '按日查询'
        },
        {
          value: '2',
          label: '按月查询'
        },
        {
          value: '3',
          label: '按年查询'
        }
      ], // 日期统计方式选项
      typeOptions: [
        {
          value: '1',
          label: '工厂质检'
        },
        {
          value: '2',
          label: '景区验收'
        }
      ], // 检验类型选项
      resultOptions: [
        {
          value: '0',
          label: '不合格'
        },
        {
          value: '1',
          label: '合格'
        }
      ], // 检验结果选项

      exportTableData: [], // 导出的全部表格数据
      downloadLoading: false, // 导出excel按钮的加载动画
      downloadReportLoading: false, // 下载报表loading

      tableLoading: false,

      currentPage: 1, // 当前页数,
      pageSize: 10, // 每页显示条数

      dialogVisible: false, // 是否显示编辑dialog对话框
      detailDialogVisible: false, // 是否显示详情dialog对话框

      detailTableList: [], // 详情列表

      dialogState: '', // dialog是新增还是编辑表单，新增为'add'，编辑为'edit'
      checklistLoading: false,

      selectDateState: '1', // 日期统计方式（日，月，年）
      dateType: 'daterange', // 日期类型（日，月，年）
      valueFormat: 'yyyy-MM-dd',

      searchValidateForm: {
        robotCode: '', // 设备编号
        startEndDate: '', // 起止日期
        type: '', // 检验类型
        result: '' // 检验结果
      }, // 搜索表单验证对象
      searchVF: {
        robotCode: '', // 设备编号
        startEndDate: '', // 起止日期
        type: '', // 检验类型
        result: '' // 检验结果
      }, // 提交表单验证对象

      validateForm: {}, // 表单验证对象

      searchSelectDateState: '1', // 搜索的日期统计方式

      companys: [], // 公司列表
      companyState: '', // 当前请选择公司

      scenics: [], // 景区列表
      scenicState: '', // 当前请选择景区

      checklistTitle: '', // 检验报告单标题
      pdfUrl: '', // 导出的pdf地址
      pdfName: '', // 导出的pdf文件名称,

      total: 0,

      currentChecklistId: 0, // 当前检验单id
      selectionIds: [], // 当前选择的检验单id列表

      // 搜索表单验证规则
      searchValidateFormRules: {},

      // 提交表单验证规则
      validateFormRules: {}
    }
  },
  created() {
    // 如果当前环境是开发环境才调用登录方法
    if (process.env.NODE_ENV == 'development') {
      this.login()
    }

    this.getValidateForm()

    this.initTableData()
  },
  mounted() {},
  methods: {
    // 获取表单验证对象
    getValidateForm() {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = ''
      }
      this.validateForm = validateForm
      // console.log(this.validateForm);
    },

    // 登录(必须)
    async login() {
      const { data: res } = await this.$http.post(
        '/loginSystem?username=lijiazhao&password=lijiazhao'
      )
    },

    /**
     * 初始化表格数据
     */
    async initTableData() {
      var robotCode = this.searchVF.robotCode
      if (robotCode == '' || !robotCode) {
        robotCode = null
      }

      var startEndDate = this.searchVF.startEndDate
      var beginDate, endDate
      if (startEndDate == '' || !startEndDate) {
        beginDate = null
        endDate = null
      } else {
        beginDate = formatDateTime(
          this.searchVF.startEndDate[0],
          this.selectDateState
        )
        endDate = formatDateTime(
          this.searchVF.startEndDate[1],
          this.selectDateState
        )
      }

      // console.log(beginDate, endDate);

      var type = this.searchVF.type
      if (type == '' || !type) {
        type = null
      }

      var result = this.searchVF.result
      if (result == '' || !result) {
        result = null
      }

      this.tableLoading = true
      const { data: res } = await this.$http.get('/inspection_record/list', {
        params: {
          robotCode: robotCode,
          startTime: beginDate,
          endTime: endDate,
          type: type,
          result: result,
          pageNum: (this.currentPage - 1) * this.pageSize, // 页码
          pageSize: this.pageSize // 每页条数
        }
      })
      // console.log(res);
      if (res.state == '200') {
        this.tableData = res.data.list
        this.total = res.data.total

        this.tableLoading = false
      }
    },

    /**
     * 获取预览列表数据
     * @params id - 检验单id
     */
    async getPreviewTableData(id) {
      const { data: res } = await this.$http.get('/inspection_record/preview', {
        params: {
          id: id
        }
      })
      if (!res.data) {
        this.$message({
          message: '预览信息为空',
          type: 'info',
          offset: 100,
          center: true,
          duration: 1000
        })
        return
      }
      return res.data
    },

    /**
     * 机器人检验记录详情信息
     */
    async detailTableDate(id) {
      const { data: res } = await this.$http.get('', {
        params: {
          // form: 1, // 1、工厂发货；2、景区调度
          id: id // 当前编辑行id
        }
      })
      var result = {}
      for (let key in res.data) {
        if (key == 'robotCount') {
          result['机器人数量'] = res.data[key]
        }
        if (key == 'applicantName') {
          result['申请人'] = res.data[key]
        }
        if (key == 'applicantTime') {
          result['申请时间'] = res.data[key]
        }
        // if (key == "consignmentDate") {
        //   result['发货时间'] = res.data[key]
        // }
        if (key == 'consignorName') {
          result['发货人'] = res.data[key]
        }
        if (key == 'factoryName') {
          result['发货工厂'] = res.data[key]
        }
        if (key == 'receiningAddress') {
          result['收货地址'] = res.data[key]
        }
        if (key == 'receiningName') {
          result['收货景区'] = res.data[key]
        }
        if (key == 'consigneeName') {
          result['收货人'] = res.data[key]
        }
        if (key == 'consigneePhone') {
          result['收货人电话'] = res.data[key]
        }
        if (key == 'receivingCount') {
          result['收货数量'] = res.data[key]
        }
        if (key == 'type') {
          if (res.data[key] == 1) {
            result['发货进度'] = '待发货'
          } else if (res.data[key] == 2) {
            result['发货进度'] = '已发货'
          } else if (res.data[key] == 3) {
            result['发货进度'] = '已收货'
          } else if (res.data[key] == 4) {
            result['发货进度'] = '已关闭'
          }
        }
        if (key == 'consignmentCount') {
          result['发货件数'] = res.data[key]
        }
        if (key == 'driverName') {
          result['驾驶员姓名'] = res.data[key]
        }
        if (key == 'driverPhone') {
          result['驾驶员电话'] = res.data[key]
        }
        if (key == 'license') {
          result['车牌号'] = res.data[key]
        }
        if (key == 'loadingCost') {
          result['运费'] = res.data[key]
        }
        if (key == 'robotCodes') {
          result['设备编码'] = res.data[key]
        }
      }

      this.detailTableList = result
    },

    // 日期选项值发生变化时触发
    onDateChange() {
      console.log(this.selectDateState)
      if (this.selectDateState == '1') {
        // 按日查询
        this.dateType = 'daterange'
        this.valueFormat = 'yyyy-MM-dd'
      } else if (this.selectDateState == '2') {
        // 按月查询
        this.dateType = 'monthrange'
        this.valueFormat = 'yyyy-MM'
      } else if (this.selectDateState == '3') {
        // 按年查询
        this.dateType = 'yearrange'
        this.valueFormat = 'yyyy'
      }
    },

    /**
     * 查询公司名称获取列表数据
     */
    getTableData() {
      // 如果输入框中的内容等于上次搜索的内容，则直接返回
      if (
        this.searchValidateForm.robotCode == this.searchVF.robotCode &&
        this.selectDateState == this.searchSelectDateState &&
        this.searchValidateForm.startEndDate == this.searchVF.startEndDate &&
        this.searchValidateForm.type == this.searchVF.type &&
        this.searchValidateForm.result == this.searchVF.result
      ) {
        return
      }

      // 保存当前搜索的内容
      this.searchVF.robotCode = this.searchValidateForm.robotCode
      this.searchSelectDateState = this.selectDateState
      this.searchVF.startEndDate = this.searchValidateForm.startEndDate
      this.searchVF.type = this.searchValidateForm.type
      this.searchVF.result = this.searchValidateForm.result

      // 否则调用接口，请求数据，为tableData重新赋值
      console.log('调用接口')
      this.initTableData()
    },
    // 点击搜索按钮时触发
    onSearch(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          // console.log(this.searchValidateForm);
          this.getTableData()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // 重置
    onReset() {
      // 清空所有输入框内容
      this.selectDateState = '1'
      this.dateType = 'daterange'
      this.searchValidateForm.robotCode = ''
      this.searchValidateForm.startEndDate = ''
      this.searchValidateForm.type = ''
      this.searchValidateForm.result = ''

      // 重新获取表格数据
      this.getTableData()
    },

    // 导出检验单
    exportExcel() {
      this.$confirm('确认导出检验单?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          this.downloadLoading = true

          if (this.selectionIds.length == 0) {
            this.$message({
              type: 'info',
              message: '未选择需要导出的检验单',
              offset: 100,
              center: true,
              duration: 1000
            })
            this.downloadLoading = false
            return
          }
          // this.selectionIds.forEach(async (selectionId, index) => {
          //   // 利用setTimeout循环切换pdfUrl，并在其中使用$nextTick来模拟点击a标签来完成下载全部核验单功能
          //   setTimeout(() => {
          //     this.pdfUrl = `/inspection_record/export?id=${selectionId}`
          //     // console.log(this.pdfUrl);
          //     this.$nextTick(() => {
          //       document.getElementById("a_pdf").click()
          //     })
          //   }, 100 * index)
          // })

          this.$nextTick(() => {
            var exportFn = async (id) => {
              const { data: res } = await this.$http.get(
                '/inspection_record/export',
                {
                  params: {
                    id
                  },
                  responseType: 'arraybuffer' // 这里必须制定响应的数据类型为arraybuffer，否则导出的文件无法打开
                }
              )

              if (res) {
                const url = window.URL.createObjectURL(
                  new Blob([res], {
                    type: 'application/pdf'
                  })
                )
                const a = document.createElement('a')
                console.log(url)
                a.href = url
                a.download = '机器人检验记录' + id
                a.click()

                this.$message({
                  message: '导出成功',
                  type: 'success'
                })
              } else {
                this.$message({
                  message: '导出失败',
                  type: 'error'
                })
              }
            }

            ;(async () => {
              for (var i = 0; i < this.selection.length; i++) {
                var item = this.selection[i]
                await exportFn(item.id)
              }
            })()
            // this.selection.forEach(async (item, index) => {
            //   var a = document.createElement('a')
            //   a.href = `/inspection_record/export?id=${item.id}` // 文件地址
            //   a.download = '' // 文件名
            //   a.click() // 模拟点击下载文件
            // })
          })
          this.downloadLoading = false
          // // 使用nextTick当table表格渲染完成后执行导出表格操作
          // this.$nextTick(() => {
          //   var xlsxParam = { raw: true } //转换成excel时，使用原始的格式
          //   /* 从表生成工作簿对象 */
          //   var wb = XLSX.utils.table_to_book(
          //     document.querySelector('#outTable'),
          //     xlsxParam
          //   )
          //   /* 获取二进制字符串作为输出 */
          //   var wbout = XLSX.write(wb, {
          //     bookType: 'xlsx',
          //     bookSST: true,
          //     type: 'array'
          //   })
          //   try {
          //     FileSaver.saveAs(
          //       //Blob 对象表示一个不可变、原始数据的类文件对象。
          //       //Blob 表示的不一定是JavaScript原生格式的数据。
          //       //File 接口基于Blob，继承了 blob 的功能并将其扩展使其支持用户系统上的文件。
          //       //返回一个新创建的 Blob 对象，其内容由参数中给定的数组串联组成。
          //       new Blob([wbout], {
          //         type: 'application/octet-stream'
          //       }),
          //       //设置导出文件名称
          //       this.name + '.xlsx'
          //     )
          //   } catch (e) {
          //     if (typeof console !== 'undefined') console.log(e, wbout)
          //   }
          //   this.downloadLoading = false
          //   return wbout
          // })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消导出Excel表格',
            offset: 100,
            center: true,
            duration: 1000
          })
        })
    },

    // 当选择项发生变化时触发
    handleSelectionChange(selection) {
      // console.log(selection);
      var selectionIds = selection.map((item) => {
        return item.id
      })
      this.selectionIds = selectionIds
      this.selection = selection
    },

    // 点击详情时触发
    detailRow(index, row) {
      console.log(index, row)
      this.detailDialogVisible = true
      // this.detailTableDate(row.id)

      var result = {}
      for (let key in row) {
        if (key == 'inspectionName') {
          result['检验标准'] = row[key]
        }
        if (key == 'inspectonTime') {
          result['检验时间'] = row[key]
        }
        if (key == 'inspectorName') {
          result['检验员'] = row[key]
        }
        if (key == 'inspectorRole') {
          result['角色'] = row[key]
        }
        if (key == 'notes') {
          result['备注'] = row[key]
        }
        if (key == 'productionInfo') {
          result['生产批次'] = row[key]
        }
        if (key == 'robotCode') {
          result['设备编号'] = row[key]
        }
        if (key == 'robotModel') {
          result['机器人型号'] = row[key]
        }
        if (key == 'type') {
          if (row[key] == 1) {
            result['检验类型'] = '工厂质检'
          } else if (row[key] == 2) {
            result['检验类型'] = '景区验收'
          }
        }
        if (key == 'result') {
          if (row[key] == 0) {
            result['检验结果'] = '不合格'
          } else if (row[key] == 1) {
            result['检验类型'] = '合格'
          }
        }
      }

      this.detailTableList = result
    },

    // 点击检验单触发
    async checklist(index, row) {
      console.log(index, row)
      this.dialogVisible = true
      this.checklistLoading = true
      var res = await this.getPreviewTableData(row.id)
      this.checklistLoading = false
      console.log(res)
      this.currentChecklistId = row.id // 保存当前检验单id
      console.log(row)
      if (row.type == 1) {
        this.checklistTitle = '园区伴游机器人质检报告'

        var checklistTableData = [
          {
            1: '公司名称',
            2: res.info.companyName,
            3: '设备编号',
            4: res.info.robotCode
          },
          {
            1: '设备型号',
            2: res.info.robotModel,
            3: '生产批次',
            4: res.info.productionInfo
          },
          {
            1: '质检标准',
            2: res.info.inspectionName,
            3: '质检结果',
            4: res.info.result == 0 ? '不合格' : '合格'
          },
          {
            1: '质检员',
            2: res.info.inspectorName,
            3: '质检日期',
            4: res.info.inspectonTime
          }
        ]
        this.checklistTableData = checklistTableData

        this.checklistTableData2 = res.details
      } else if (row.type == 2) {
        this.checklistTitle = '园区伴游机器人验收单'

        var checklistTableData = [
          {
            1: '景区名称',
            2: res.info.scenicSpotName,
            3: '设备编号',
            4: res.info.robotCode
          },
          {
            1: '设备型号',
            2: res.info.robotModel,
            3: '生产批次',
            4: res.info.productionInfo
          },
          {
            1: '验收标准',
            2: res.info.inspectionName,
            3: '验收结果',
            4: res.info.result == 0 ? '不合格' : '合格'
          },
          {
            1: '验收员',
            2: res.info.inspectorName,
            3: '验收日期',
            4: res.info.inspectonTime
          }
        ]
        this.checklistTableData = checklistTableData

        this.checklistTableData2 = res.details
      }
    },

    // 点击检验单dialog 右上角关闭按钮触发
    handleClose(done) {
      done()
    },

    // 点击检验单dialog 下载按钮触发
    onDownloadPDF() {
      this.$confirm('确认下载？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async (_) => {
          this.downloadReportLoading = true

          const { data: res } = await this.$http.get(
            '/inspection_record/export',
            {
              params: {
                id: this.currentChecklistId
              },
              responseType: 'arraybuffer' // 这里必须制定响应的数据类型为arraybuffer，否则导出的文件无法打开
            }
          )

          if (res) {
            const url = window.URL.createObjectURL(
              new Blob([res], {
                type: 'application/pdf'
              })
            )
            const a = document.createElement('a')
            console.log(url)
            a.href = url
            a.download = '机器人检验记录' + this.currentChecklistId
            a.click()

            this.$message({
              message: '导出成功',
              type: 'success'
            })
          } else {
            this.$message({
              message: '导出失败',
              type: 'error'
            })
          }

          // this.pdfName = this.checklistTitle
          // this.pdfUrl = `/inspection_record/export?id=${this.currentChecklistId}`
          // this.$nextTick(() => {
          //   console.log(document.getElementById('a_pdf'))
          //   document.getElementById('a_pdf').click()
          // })
          this.downloadReportLoading = false
          //   console.log(this.$PDFOutput);
          //   this.downloadReportLoading = true
          //   var promiseObj = this.$PDFOutput(this.$refs["checklistTable"])
          //   console.log(promiseObj);
          //   promiseObj.then((value) => {

          //     this.pdfUrl = value
          //     // this.dialogVisible = false // 关闭dialog对话框
          //     this.downloadReportLoading = false

          //     this.pdfName = this.checklistTitle
          //     document.getElementById("a_pdf").click()

          //   this.$message({
          //     type: 'success',
          //     message: '下载成功'
          //   });
          // })
        })
        .catch((_) => {
          this.$message({
            type: 'info',
            message: '已取消下载',
            offset: 100,
            center: true,
            duration: 1000
          })
        })
    },

    // 详情 dialog 右上角关闭按钮
    detailHandleClose(done) {
      done()
    },

    // currentPage 改变时会触发
    onCurrentChange(currentPage) {
      this.currentPage = currentPage
      this.initTableData()
    },

    // pageSize 改变时会触发
    onSizeChange(pageSize) {
      // console.log(pageSize);
      this.pageSize = pageSize
      this.initTableData()
    }
  },
  components: {
    // DownloadExcel: JsonExcel,
  },
  computed: {
    tableContentValue() {
      return (row, value) => {
        var result = ''
        if (value == 'type') {
          if (row[value] == 1) {
            result = '工厂质检'
          } else if (row[value] == 2) {
            result = '景区验收'
          }
        } else if (value == 'result') {
          if (row[value] == 0) {
            result = '不合格'
          } else if (row[value] == 1) {
            result = '合格'
          }
        } else {
          result = row[value]
        }
        return result
      }
    },

    checklistTableData2Scope() {
      return (row, value) => {
        var result = ''
        if (value == 'qualified') {
          if (row[value] == 0) {
            result = '不合格'
          } else if (row[value] == 1) {
            result = '合格'
          }
        } else {
          result = row[value]
        }
        return result
      }
    }
  }
}
</script>

