/**
 * 格式化时间
 */
export function formatDateTime(datetime, type = "1") {
  console.log(type);
  var year = datetime.getFullYear().toString();
  var month = (datetime.getMonth() + 1).toString().padStart(2, "0");
  var date = datetime
    .getDate()
    .toString()
    .padStart(2, "0");
  var currentDate = "";
  if (type == "1") {
    // 格式化为日
    currentDate = year + "-" + month + "-" + date;
  } else if (type == "2") {
    // 格式化为月
    currentDate = year + "-" + month;
  } else if (type == "3") {
    // 格式化为年
    currentDate = year;
  }
  return currentDate;
}
